import React, { useState }  from 'react';
import { 
	LinksWrapper, 
	TabWrapper, Tab, 
	ContentWrapper, 
	LinksList, 
	Expansion,
	LinkItem 
} from './style';

const linkListContent = [
	"", "", "app","", "", "java", "", 
	"", "", "", "", "", 
	"", "", "", "", "",
	"", "", "", "", "", "",
	"", "", "", "", "", "", 
	"", "", "", "", "", "","",
	"", "", "", "", "","", "",
	"", "", "", "", "", "", "",
	 "", "", "", "", "", "",
	 "", "", ""]

const Links = (props) => {
	const [expand, setExpand] = useState(false);
	const handleClick = () => {
		expand ? setExpand(false) : setExpand(true)
	}
	return (
		<LinksWrapper>
			<TabWrapper>
				<Tab ></Tab>
			</TabWrapper>
			<ContentWrapper>
				<LinksList>
					{
						expand ?
						linkListContent.map((item, index) => {
							return <LinkItem key={index} >{item}</LinkItem>
						})
						:
						linkListContent.slice(0, 14).map((item, index) => {
							return <LinkItem key={index} >{item}</LinkItem>
						})						
					}
				</LinksList>
				<Expansion expand={expand} onClick={handleClick}>
					{expand ? "" : ""}
					<span className="iconfont" >&#xe70a;</span>
				</Expansion>
			</ContentWrapper>
		</LinksWrapper>
	)
}

export default Links;