import React , { Component } from 'react';
import { JobWrapper, MoreBtn } from './style';
import JobTab from './JobTab';
import JobList from './JobList';

class Job extends Component{
	render(){
		return(
			<JobWrapper>
				<JobTab />
				<JobList />
				<MoreBtn ></MoreBtn>
			</JobWrapper>
		)
	}
}

export default Job;