import React from 'react';
import { 
	PopupCityWrapper,
	Content,
	SiteList,
	SiteBtn,
	SiteBtnActive
} from './style';


const PopupCity = (props) => {
	return(
		<PopupCityWrapper showCity={props.showCity}>
			<Content>
				<div className="title">
					<span className="switch"></span>
					<span 
						className="iconfont close" 
						onClick={props.handleClickCity}
					>
						&#xe621;
					</span>
				</div>
				<div className="greeting"> 
					<div className="line1">
						
					</div>
					<div className="line2">
						
					</div>
				</div>
				<SiteList>
					<div className="info">
						<span></span>
						<SiteBtnActive></SiteBtnActive>
						<span>or&nbsp;</span>	
					</div>
					<div className="list">
						<SiteBtn></SiteBtn>
						<SiteBtn></SiteBtn>
						<SiteBtn></SiteBtn>
						<SiteBtn></SiteBtn>
						<SiteBtn></SiteBtn>
						<SiteBtn></SiteBtn>
						<SiteBtn></SiteBtn>
						<SiteBtn></SiteBtn>
					</div>
					<div className="footer">
						
					</div>
				</SiteList>
			</Content>
		</PopupCityWrapper>
	)
}

export default PopupCity;