import React, { useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { actionCreator } from './store';
import { 
	HeaderWrapper,
	Logo,
	SuggestCity,
	NavBarMiddle,
	NavItem,
	NavBarRight,
	Passport,
	PassportIcon,
	PassportItem,
	AppDownload,
	AppIcon,
} from './style';
import PopupCity from './popupCity';
import PopupLoginAndReg from './popupLogAndReg';

const Header =(props) => {
	const dispatch = useDispatch();

	const showCity = useSelector(state => state.header.showCity);
	const showLogAndReg = useSelector(state => state.header.showLogAndReg);

 	const handleClickCity = useCallback(
 		() => dispatch(actionCreator.showCity()),
    	[dispatch]
  	);
 	const handleClickLogin = useCallback(
 		() => dispatch(actionCreator.clickLogin()),
    	[dispatch]
  	);
  	const handleClickReg = useCallback(
 		() => dispatch(actionCreator.clickReg()),
    	[dispatch]
  	);

	return (
		<HeaderWrapper>
			<Logo/>
			<SuggestCity>
				<span></span>
				<em 
					className="switch" 
					onClick={handleClickCity}
				>
					[]
				</em>										
			</SuggestCity>
			<NavBarMiddle>
				<NavItem className="active"></NavItem>
				<NavItem href="https://www.lagou.com/gongsi/"></NavItem>
				<NavItem href="https://xiaoyuan.lagou.com/"></NavItem>
				<NavItem href="https://yanzhi.lagou.com/"></NavItem>
				<NavItem href="https://kaiwu.lagou.com/"></NavItem>					
			</NavBarMiddle>					
			<NavBarRight> 
				<Passport>
					<PassportIcon/>
					<PassportItem onClick={handleClickLogin}></PassportItem>
					<PassportItem className="span">|</PassportItem>
					<PassportItem onClick={handleClickReg}></PassportItem>
				</Passport>
				<AppDownload href="https://www.lagou.com/app/download.html">
					<AppIcon className="icon"/>
					<span className="text">APP</span>
				</AppDownload>
				<NavItem href="https://easy.lagou.com/dashboard/index.htm?from=c_index"></NavItem>
			</NavBarRight>
			<PopupCity showCity={showCity} handleClickCity={handleClickCity}/>
			<PopupLoginAndReg showLogAndReg={showLogAndReg}/>
		</HeaderWrapper>
	)	
} 

export default Header;